.references {
  scroll-margin-top: 68px;

  @include vp-767 {
    scroll-margin-top: 0;
  }
}

.references__wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.references__list {
  display: flex;
  align-items: center;
  justify-content: space-around;
  width: 100%;

  li {
    padding: 10px;
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  h3 {
    padding: 0;
    margin: 0;
  }

  p {
    padding: 0 0 0 20px;
    margin: 0;
  }
}
